*{
    font-family: 'Roboto';
    font-weight: 100;
    letter-spacing: 2px;
    margin: 0px;
    padding: 0px;
    text-transform: capitalize;
}

header{
    border-bottom: 1px solid black;
    background-color:#fe000f;
    padding-bottom: 25px;
}

.aviso{
    width: 80%;
    margin: 0px auto;
    margin-top: 100px;
    text-align: center;
    animation: incremento 3s infinite ;
}

@keyframes incremento {
    from{
        transform: scale(1,1);
    }

    50%{
        transform: scale(1.2,1.2);
    }

    to{
        transform: scale(1,1);
    }
}



.Contenedor_Global{
    border-radius: 20px;
    border: 5px solid #fe000f;
    box-shadow: 0px 0px 4px #fe000f, 1px 1px 5px #fe000f, 2px 2px 6px #fe000f;
    width: 80%;
    margin: 50px auto;
    min-height: 700px;
    background: whitesmoke;
}

header h1{
    width: 90%;
    margin:  0px auto;
    padding-top: 20px;
    margin-bottom: 10px;
    color: white;
    text-align: center;
}

.Ingresar_Pokemon{  
    text-align: center;
}

.Ingresar_Pokemon input{
    width: 50%;
    height: 25px;
    border-radius: 30px;
    padding-left: 15px;
    border: 5px solid #3752b5;
    transition: all 300ms;
    box-shadow: 0px 0px 4px #252070;
    margin-top: 20px;
    margin-bottom: 20px;
     background-color: #ffd800;   
}



.fichaPokemon{   
    margin-top: 50px;
    min-height: 770px;
}

.imagen_pokemon{
    transition: all 200ms;
}

.imagen_pokemon:hover{
    transform: scale(1.3, 1.3);
}

.ficha{
    width: auto; 
    text-align: center; 
    border:1px solid black;
    grid-column: 4/5;

}

.ficha h1{
    width: auto;
    color: white;
    background-color: #230443;
    padding: 10px;
    border-radius: 10px 10px 0px 0px;
    text-align: center;
}

.ficha img{
    width: 50%;
}

.estadisticas_grid{
    display: grid;
    grid-template-columns: 17.5% 30% 5% 30% 17.5%;     
}

.contenedor{
    width: auto;
    border: 1px solid black;
    border-radius: 10px 10px 50px 10px;
    margin-bottom: 50px;
    box-shadow: 0px 0px 1px #ccc, 1px 1px 2px #ccc, 2px 2px 3px #ccc, 3px 3px 4px #ccc, 4px 4px 5px #ccc;
}

.stats h3{
    width: auto;
    color: white;
    background-color: red;
    padding: 10px;
    border-radius: 10px 10px 0px 0px;
}

.stats ul{
    padding: 15px;
    list-style: none;
    line-height: 20px;
}

.habilidades h3{
    color: white;
    width: auto;
    background-color: #2823bc;
    padding: 10px;
    border-radius: 10px 10px 0px 0px;    
}

.habilidades ul{
    padding: 15px;
    list-style: none;
    line-height: 20px;
}

.habilidades_y_stats{    
    grid-column: 2/3;
    grid-row: 1/3;
}

.tipos{
    display: flex;
    flex-direction: row;
    text-align: center;
    padding-bottom: 20px;
    
}

.tipos h2{
    border: 1px solid black;
    border-radius: 5px;
    box-shadow: 0px 0px 3px grey;
    color: white;
    margin: 0px auto;  
    padding: 5px;
    text-align: center;
    width: 85px;
    font-size: 100%;
}

.movimientos{
    border-radius: 10px;
    border: 1px solid black;
    margin-left: 190px;
    width: 65%;
    box-shadow: 0px 0px 1px #ccc, 1px 1px 2px #ccc, 2px 2px 3px #ccc, 3px 3px 4px #ccc, 4px 4px 5px #ccc; 
}

.movimientos h3{
    color: white;
    width: auto;
    background-color: #2823bc;
    padding: 10px;
    border-radius: 10px 10px 0px 0px;    
}

.movimientos ul{
    border-top: 1px solid black;
    width: 100%;
    height: 250px;
    overflow-y: scroll;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    
}

.movimientos ul li{
    width: 117px;
    margin: 10px auto;
    text-align: center;
}

.elemento{
    transition: all 100ms ;
    display: block;
    
}

.elemento:hover{
    font-weight: bolder;
    background-color: #c3c3c3;
    cursor: pointer;
}

.footer{
    width: 100%;
    height: 100px;
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
}

.version{
    margin: 20px;
}

.datos_autor{
    margin-top: 20px;
    margin-right: 30px;
}

.datos_autor h5{
    font-size: 15px;
    margin-bottom: 10px;
}

.datos_autor p{
    margin-top: 5px;
}

.datos_autor a{
    text-decoration: none;
    color: white;
    font-size: 15px;
}

.datos_autor img{
    margin-left: 15px;
}



.bug{
    background-color: #9ac800;
}

.dark{
    background-color: #735946;
}

.dragon{
    background-color: #9100fd;
}

.electric{
    background-color: #f8dd00;
}

.fairy{
    background-color: #fd8baa;
}

.fighting{
    background-color: #d00014;
}

.fire{
    background-color: #fe7c06;
}

.flying{
    background-color: #b86ff3;
}

.ghost{
    background-color: #7b409a;
}

.grass{
    background-color: #40da48;
}

.ground{
    background-color: #e1c95e;
}

.ice{
    background-color: #85dcd9;
}

.normal{
    background-color: #a4af75;
}

.poison{
    background-color: #b200a2;
}

.psychic{
    background-color: #ff0e84;
}

.rock{
    background-color: #b7aa26;
}

.steel{
    background-color: #bbb3d1;
}

.water{
    background-color: #7375f4;
}


/* media queries */


@media ( max-width:1080px ){
    .movimientos {
        margin-left: 151px;
    }
}

@media ( max-width:926px ){
    .movimientos {
        margin-left: 130px;
    }
}

@media ( max-width:810px ){
    .movimientos {
        margin-left: 113px;
        width: 65%;
    }

    .tipos h2{
        width: 70px;
        font-size: 80%;
        padding: 0px;
    }
}


@media ( max-width:428px ){
    .estadisticas_grid {
        display: block;
      }
    
    .query-block{
        width: 80%;
        margin: 20px auto;
    }
}